import { Headers } from '@angular/http';
import { Cookie } from 'ng2-cookies/ng2-cookies'


export class ApiService {

    private uid = this.getCookie("uid");
    private token = this.getCookie("token") + "==";
    private authorId = Number(this.getCookie("authorId"));

    constructor() { }

    getUrl(): string {
        return 'api/v1';
    }
    getHeaders(): Headers {
        return new Headers({ 'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8',
         'FromAgent': 'Broswer' ,
         'sso_id': this.uid,
         'sso_token':this.token
        });
    }

    getCookie(name: string): String {
        return Cookie.get(name);
    }

    setCookie(c_name, value) {
        Cookie.set(c_name,value,null,'/')
    }
}
