import { Component, OnInit } from '@angular/core';
import { ForumService } from '../../service/forum.service';
import { Router } from '@angular/router';
import { Cookie } from 'ng2-cookies/ng2-cookies'

@Component({
	selector: 'app-forum-index',
	templateUrl: './forum-index.component.html',
	styleUrls: ['./forum-index.component.css']
})
export class ForumIndexComponent implements OnInit {

	constructor(private forumService: ForumService, private router: Router) { }
	modal = false;
	uid = "";
	token = "";
	ngOnInit() {
		//Cookie.deleteAll()
		// Cookie.set("uid","1",null,'/');
		// Cookie.set("token","UhZKj7i8YSrapGbIxeqa1g",null,'/');
		//console.log(document.cookie);
		this.uid = (Cookie.get('uid')).toString();
		this.token = Cookie.get('token') + "==";
		if (this.uid != "" && this.token != "") {

			// this.forumService.login(this.uid, this.token).then(res1=>{
			// 	if(res1.state==true){
			this.forumService.joinForum(this.uid, this.token).then(res2 => {
				if (res2.state == true) {
					Cookie.delete("authorId");
					Cookie.set("authorId", res2.data.authorId,null,'/');
					this.forumService.getauthorId();
					this.modal = false;
				} else {
					this.modal = true;
				}
			})
			// 	}else{
			// 		this.modal=false;
			// 	}

			// });
		}
	}

	addName(): void {
		let name = $("#nickname").val().toString();
		if (name.length > 12) {
			alert("你能不能短一点");
			return;
		}
		this.forumService.addName(name, this.uid, this.token).then(res => {
			if (res.state == true) {
				Cookie.delete("authorId");
				Cookie.set("authorId", res.data.authorId);
				this.forumService.getauthorId();
				this.modal = false;
			}
			else {
				alert(res.msg);
			}
		});
	}

}
